
.loot-box {
    border: 1px solid #ffffff14;

    .song-selector-opt {
        border-radius: 10px !important;
        width: 50px;
        height: 50px;
    }

    //.song-option-container:hover { /*, .song-option-container.current-playing {*/
    .song-option-container:hover { //, .song-option-container.current-playing {
        .hover-over {
            display: block;
        }
    }
    .song-option-container:hover.loading-song { //, .song-option-container.current-playing {
        .hover-over {
            display: none;
        }
    }

    .song-option-container.loading-song {
          animation: bounce-load 1.5s cubic-bezier(0.5, 0.05, 0.35, 1.5) infinite;

        transform-origin: center bottom;
    }

    .song-option-container.current-playing {
        .hover-over {
            opacity: 0;
            animation-name: disappear-hover;
            animation-duration: 1s;
        }
    }


    .song-option-container {
        position: relative;

        .hover-over::after {
            content: "";
            position: absolute;
            left: 0px;
            right: 0px;
            margin-left: auto;
            margin-right: auto;
            bottom: -12px;
            width: 0;
            height: 0;
            border-color: transparent;
            border-top-color: #ffffffd9;
            border-style: solid;
            border-width: 12px 8px 0;
        }
        .hover-over {
            .song-name {
                color: #c1c1c1;
            }

            .artist-name {
                color: #807f7f;
            }
            border-radius: 10px;
            background-color: #4545454f;
            border: 1px solid white;
            background-color: #141313d9;
            color: #c3c3c3;
            display: none;
            position: absolute;
            left: -35%;
            top: -90px;
            white-space: nowrap;
            padding: 8px;
            padding-left: 10px;
            padding-right: 10px;
        }
    }

    .song-option-container.current-playing::after {
        content: "";
        width: 6px;
        height: 6px;
        position: absolute;
        left: 45%;
        bottom: -12px;
        background-color: white;
        border-radius: 8px;
    }


    position: absolute;
    bottom: 0px;
    left: 0px;
    right: 0px;
    margin-left: auto;
    margin-right: auto;
    z-index: 1000000000;
    //background-color: red;
    display: flex;
    padding: 20px;
    //backdrop-filter: blur(8px);
    //background-color: #0000003b;
    padding: 10px;
    padding-bottom: 15px;
    max-width: 495px;
    border-radius: 32px;
    background-color: #4545454f;
    bottom: 15px;

    .zorb-selector {
        margin-left: auto;
        padding: 1px;
        padding-left: 8px;
        border-left: 1px solid #4c4c4c;
        width: 80px;
    }



    svg {
        width: 300px !important;
    }

    .loot-inner {
        width: 100%;
        //        max-width: 300px;
        //       flex-wrap: wrap;
        display: flex;
        
        
        .loot-option {
            margin: 3px;
            img {
                width: 20px;
                height: 20px;
            }
        }
    }
}

.bigger.loot-box {
    .loot-inner {
        .loot-option {
            img {
                width: 30px;
                height: 30px;
            }
        }
    }
}

@keyframes bounce-load {
     0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-15px);
  }
}

@keyframes disappear-hover {
    0% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}
